.hero {
  background: var(--bg); /* or use the brand-dark gradient if you prefer */
  color: var(--ink);
  text-align: center;
  padding: 120px 0 80px;
}
.title {
  font-size: var(--h1);
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: -0.015em;
  margin-bottom: 20px;
}
.lead {
  font-size: var(--lead);
  color: var(--muted);
  max-width: 60ch;
  margin: 0 auto 40px;
  line-height: 1.6;
}
.actions {
  display: flex;
  justify-content: center;
  gap: 14px;
  flex-wrap: wrap;
}
